import {commumal,listWindow,descroll} from "../public"
var myReimburse={
    methods:{
        init(){
            $(".weui-btn.query").on("click",myReimburse.events.openWindow);
            $(".content").scroll(myReimburse.events.scroll);
            $(".condition ul li i").on('click',listWindow.defaultDelete)
        }
    },
    events:{
        openWindow(){
            listWindow.backToStartPoint()
            listWindow.query({
                cache:"DATE",
                loadCondition:(data)=>{
                    myReimburse.events.loadCondition(data)
                },
                fresh:(data)=>{
                    myReimburse.events.fetchData(data,{refresh:true,complete:false})
                }
            })
        },
        loadCondition(data){
            let html="";
            for(let i in data){
               html+=`
                 <li value="${data[i].value.index}">
                     <span>${data[i].name}</span>
                     <i class="iconfont icon-x"></i>
                 </li>
               `
            }
           let $html=$(html);
            $html.find("i").on("click",myReimburse.events.deleteli)
            $(".condition ul").html($html)
        },
        deleteli(){
            listWindow.backToStartPoint()
            let $li=$(this).closest("li");
            let index=$li.attr("value");
            let arr=JSON.parse(commumal.caching("DATE"));
            for(let i=0;i<arr.length;i++){
                if(arr[i].value.index==index){
                    arr.splice(i,1)
                }
            }
            myReimburse.events.fetchData(arr,{refresh:true,complete:false})
            localStorage.setItem("DATE",JSON.stringify(arr))
            $li.remove()
        },
        fetchData(data,ref){
            let submitData={};
            for(let i in data){
                if(data[i].value.index==0){
                   submitData.QUERY=data[i].value.value   //取input框里面的内容
                }else{
                    let time=data[i].value.value;
                    submitData.DATE_FRM=time.start;
                    submitData.DATE_TO=time.end;   //取开始结束时间
                }
            }
            if(ref.refresh){
                descroll.config.page.index=1;
              }
            let date=moment().format('YYYY-MM-DD');
            let endData={
                m:"LIST",
                SP_NAME:"SP_RBRB_REIMBURSE_INFO_LIST",
                SELF_IND:"Y",
                QUERY:submitData.QUERY||"",
                DATE_FRM:submitData.DATE_FRM||date,
                DATE_TO:submitData.DATE_TO||date,
                page:descroll.config.page.index++,
                rows:descroll.config.page.rows
            }
             myReimburse.events.fresh(endData,ref)
            
        },
        fetchCache(ref){
            let arr=JSON.parse(commumal.caching("DATE"));
            let time=moment().format('YYYY-MM-DD');
            if(!arr){
                let value=[{name:"今天",value:{index:1,value:{start:time,end:time}}}]
                myReimburse.events.fetchData(value,ref);
                $(".condition ul li").attr("value",1)
            }else{
                myReimburse.events.loadCondition(arr);
                myReimburse.events.fetchData(arr,ref)
            }
        },
        fresh(subData,ref){
            $.defs.loadingshow();
            $.defs.myPost(subData).then((res)=>{
                $.defs.loadinghide();
                let html=myReimburse.events.content(res.rows);
                let $html=$(html);
                $html.find(".personDelete").on("click",myReimburse.events.delete);
                $html.on("click",myReimburse.events.matterClick);
                descroll.config.pages(res);
                if(ref.complete){
                    if(res.rows.length!=0){
                         $(".content").append($html);
                          descroll.config.isLoad=true;
                    }else{
                        $(".content").empty()
                    }
                }else{
                    if(res.rows.length!=0){
                        $(".content").html($html)
                    }else{
                        $(".content").empty()
                    }
                }
                 
            })
        },
        content(data){
            var html='';
            for(let i=0;i<data.length;i++){
                html+=`
                 <div class="weui-form-preview personInfo item" rbrb_sts="${data[i].RBRB_STS}" rbrb_ky="${data[i].RBRB_KEY_STRING}">
                   <div class="weui-form-preview__hd">
                     <label class="weui-form-preview__label black">${data[i].MEME_NAME}</label>
                     <span class="weui-form-preview__value black">￥${data[i].RBRB_APPLY_AMT}</span>
                   </div>
                   <div class="weui-form-preview__bd">
                     <div class="weui-form-preview__item">
                         <label class="weui-form-preview__label black">报销用途:${data[i].PURPOSE}<span class="ml10">报销类型:${data[i].FEPP_NAME}</span></label>
                     </div>
                     <div class="weui-form-preview__item">
                        <label class="weui-form-preview__label black">单号:${data[i].RBRB_ID_STRING}</label>
                        <span class="weui-form-preview__value black">${data[i].RBRB_STS_DESC}</span>
                     </div>
                     <div class="weui-form-preview__item">
                         <label class="weui-form-preview__label black">发生日期:${data[i].RBRB_APPLY_DT}
                    </div>
                   </div>
                   <div class="weui-form-preview__ft" style="line-height:50px">${myReimburse.events.operationBtn(data[i])}</div>
                 </div>
                `
            }
            return html;

        },
        operationBtn(data){
            let html='';
            if(data.RBRB_STS == '221'){
                html+=`<a class="weui-form-preview__btn weui-form-preview__btn_primary personDelete" href="javascript:">删除</a>`
            }
            return html
        },
        delete(ev){
            ev.stopPropagation();
            ev.preventDefault();
            let ky=$(this).closest('.personInfo').attr('rbrb_ky');
            commumal.confirm({
                title: '你确定要删除吗？',
                click:()=>{
                    myReimburse.events.deleteItem(ky)
                }
            })
        },
        deleteItem(ky){
            $.defs.loadingshow("正在删除");
            $.defs.myPost({
                SP_NAME: 'SP_RBRB_REIMBURSE_INFO_STATUS_UPDATE',
                m: 'UPDATE',
                RBRB_KY: ky,
                REVIEW_IND: 'D'
            }).then((data)=>{
                $.defs.loadinghide();
                if(data.RETURN_CODE==0){
                    myReimburse.events.fetchCache({refresh:true,complete:false});
                    weui.toast('删除成功', 3000);
                }else{
                   $.defs.msg(data.RETURN_MESSAGE)
                }
            })
        },
        matterClick(){
            var that = this;
            var ky = $(that).closest('.personInfo').attr('rbrb_ky');
            spspa.setMessage("param",{
                content: {
                    RBRB_KY: ky
                }
            })
            if ($(that).closest('.personInfo').attr('rbrb_sts')<241) {
                spspa.go("/add")
             } else {
                 spspa.go("/myQuery")
             }; 
        },
        scroll(){
            var that=this;
            descroll.events.onscroll(that,{
                datafresh:function () {
                       myReimburse.events.fetchCache({refresh:false,complete:true})
                }
            })
        }


    },
    config:{
        init(){
            $(".top .huan").text("报销管理>我的");
             myReimburse.events.fetchCache({refresh:true,complete:false})  //打开或手动刷新页面取缓存
        }
    }
}
$(function(){
     myReimburse.config.init()
     myReimburse.methods.init()
})


